.ImgIconContainer{
    width: 38px;
    height: 38px;
    border-radius: 50%;
    border: 1px solid #d3d5d8;
    display: flex;
    justify-content: center;
    align-items: center;
    margin:5px ;
}
.ImgIconContainer img{
    width: 38px;
    height: 38px;
    border-radius: 50%;
    cursor: pointer;
}