.Layout{
    margin-top: 60px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.MainFeed{
    width: 800px;
  /*   border: 1px solid green;  */
    
}
.CreatePostContainer{
    display: flex;
    justify-content: center;
    width: 100%;
}
.postListContainer{
    width: 800px;
/*     border: 1px solid blue; */
    display: flex;
    justify-content: center;
}
.postList{
    width: 80%;


}

@media(max-width:700px){
    .MainFeed{
        width: 100%;
      /*   border: 1px solid green;  */
     
    }
    .postListContainer{
        width: 100%;
    /*     border: 1px solid blue; */
        display: flex;
        justify-content: center;
    }
}